import { createRoot } from 'react-dom/client';

import { computeBoardDimensions } from './components/Board';
import App from './App';

import './index.css';
import 'animate.css';

function debounce(fn, wait) {
	let timerID;
	return function () {
		clearTimeout(timerID);
		timerID = setTimeout(fn, wait);
	};
}

window.addEventListener('resize', debounce(computeBoardDimensions, 250));

const root = createRoot(document.getElementById('root'));
root.render(<App />);
