import {
	DialogContent,
	Stack,
	Box,
	Divider,
	DialogActions,
	Button,
} from '@mui/material';
import { Dialog } from './InstructionsDialog';
import { Share } from '@mui/icons-material';

export default function StatsDialog({
	stats,
	guessDist,
	highlight,
	onReset,
	onNewGame,
	...props
}) {
	return (
		<Dialog title='Statistics' {...props}>
			<DialogContent>
				<Stack
					direction='row'
					spacing={2}
					divider={<Divider orientation='vertical' flexItem />}
					justifyContent='center'
				>
					{Object.entries(stats).map(([k, v]) => (
						<Box
							sx={{
								display: 'flex',
								flexDirection: 'column',
								alignItems: 'center',
								maxWidth: 50,
							}}
							key={k}
						>
							<Box sx={{ fontSize: 'h4.fontSize' }}>{v}</Box>
							<Box
								sx={{
									fontSize: '0.75rem',
									textAlign: 'center',
									color: 'grey.700',
									letterSpacing: 1,
								}}
							>
								{k}
							</Box>
						</Box>
					))}
				</Stack>
				<Box
					sx={{
						textAlign: 'center',
						fontWeight: 'bold',
						letterSpacing: 1,
						my: 3,
					}}
				>
					GUESS DISTRIBUTION
				</Box>
				<Stack
					spacing={0.75}
					sx={{
						fontSize: '1.2rem',
						fontWeight: 'bold',
						mx: { xs: 0, sm: 4 },
					}}
				>
					{guessDist.map((v, i) => {
						let width = Math.floor((v / Math.max(...guessDist)) * 100);

						if (isNaN(width) || width === 0) width = 'auto';
						else width = `${width}%`;

						return (
							<Box
								key={i}
								sx={{
									display: 'flex',
									alignItems: 'stretch',
									height: 25,
								}}
							>
								<Box
									sx={{
										mr: 1,
										fontWeight: 'normal',
										fontSize: '1rem',
										display: 'flex',
										alignItems: 'center',
									}}
								>
									<span>{i}</span>
								</Box>
								<Box sx={{ flexGrow: 1 }}>
									<Box
										sx={{
											display: 'inline-flex',
											alignItems: 'center',
											justifyContent: 'end',
											backgroundColor:
												highlight === i
													? 'common.green.100'
													: 'grey.500',
											color: 'common.white',
											px: 0.5,
											width,
											height: 1,
										}}
									>
										<span>{v}</span>
									</Box>
								</Box>
							</Box>
						);
					})}
				</Stack>
			</DialogContent>
			<DialogActions sx={{ mb: 1, mx: 1 }}>
				<Button
					variant='outlined'
					color='info'
					onClick={() => {
						onNewGame();
						props.onClose();
					}}
					size='small'
				>
					New Game
				</Button>
				<Button
					variant='outlined'
					color='success'
					endIcon={<Share />}
					size='small'
				>
					Share
				</Button>
				<Button
					variant='outlined'
					color='error'
					onClick={onReset}
					size='small'
				>
					Reset Stats
				</Button>
			</DialogActions>
		</Dialog>
	);
}
