import { useState } from 'react';
import { Box, Typography, IconButton, Stack } from '@mui/material';
import { Settings, LeaderboardSharp, Help } from '@mui/icons-material';

import InstructionsDialog from './InstructionsDialog';
import SettingsDialog from './SettingsDialog';
import StatsDialog from './StatsDialog';

const iconStyle = {
	color: 'common.black',
};

export default function Header({ onClick, ...props }) {
	const [openI, setOpenI] = useState(false),
		[openS, setOpenS] = useState(false);
	return (
		<>
			<Box
				component='header'
				sx={{
					position: 'relative',
					borderBottom: 1,
					borderColor: 'grey.400',
					p: { xs: 1.5, sm: 1.8 },
					display: 'flex',
					alignItems: 'center',
					justifyContent: { xs: 'space-between', sm: 'center' },
					mb: 3,
				}}
			>
				<Typography
					component='h1'
					variant='h4'
					sx={{
						fontWeight: 'bold',
						fontSize: { xs: '1.65rem', sm: 'h4.fontSize' },
						fontFamily: '"Arvo", serif',
						pl: { xs: 1, sm: 0 },
					}}
					className='title'
				>
					Wordle
				</Typography>
				<Stack
					direction='row'
					spacing={0.75}
					sx={{ position: { sm: 'absolute' }, right: 16 }}
				>
					<IconButton sx={iconStyle} onClick={() => setOpenI(true)}>
						<Help />
					</IconButton>
					<IconButton sx={iconStyle} onClick={onClick}>
						<LeaderboardSharp />
					</IconButton>
					<IconButton sx={iconStyle} onClick={() => setOpenS(true)}>
						<Settings />
					</IconButton>
				</Stack>
			</Box>
			<InstructionsDialog open={openI} onClose={() => setOpenI(false)} />
			<SettingsDialog open={openS} onClose={() => setOpenS(false)} />
			<StatsDialog {...props} />
		</>
	);
}
