import { Button as MUIButton, Box, Stack } from '@mui/material';
import { Backspace, KeyboardReturn } from '@mui/icons-material';

// import { SettingsContext } from '../App';
import './keyboard.css';

const KEYS = ['qwertyuiop', 'asdfghjkl', 'zxcvbnm'],
	keyStyles = {
		minWidth: 0,
		padding: 0,
		backgroundColor: 'grey.300',
		color: 'common.black',
		'&:hover': {
			backgroundColor: 'grey.400',
		},
		boxShadow: 0,
		fontSize: { sm: 'body1.fontSize' },
	};

const Button = props => (
	<MUIButton variant='contained' disableElevation className='key' {...props} />
);

export default function KeyBoard({ colorHints, ...rest }) {
	// const [{ highContrast, darkMode }, setUserSettings] =
	// 	useContext(SettingsContext);

	return (
		<Stack className='keyboard' alignItems='center' m={2} mt={3}>
			{KEYS.map((keys, i) => {
				return (
					<Box key={i} className='row'>
						{i === 2 && (
							<Button data-letter='enter' {...rest} sx={keyStyles}>
								<KeyboardReturn />
							</Button>
						)}

						{keys.split('').map(k => {
							let color = colorHints[k.charCodeAt() - 97];
							rest.sx = keyStyles;

							if (color) {
								// if (highContrast) {
								// 	if (color === 'green') color = 'orange';
								// 	else if (color === 'yellow') color = 'blue';
								// }
								rest.sx = {
									...keyStyles,
									backgroundColor: `common.${color}.100`,
									color: 'common.white',
									'&:hover': {
										backgroundColor: `common.${color}.200`,
									},
								};
							}

							return (
								<Button key={k} data-letter={k} {...rest}>
									{k}
								</Button>
							);
						})}

						{i === 2 && (
							<Button data-letter='backspace' {...rest} sx={keyStyles}>
								<Backspace />
							</Button>
						)}
					</Box>
				);
			})}
		</Stack>
	);
}
