const snackbarRoot = document.createElement('div');
snackbarRoot.id = 'snackbar-root';

document.body.appendChild(snackbarRoot);

export default function enqueueSnackbar(message, ms = 750) {
	const div = document.createElement('div');
	div.className = 'snackbar';
	div.innerHTML = message;
	snackbarRoot.appendChild(div);

	setTimeout(() => {
		div.onanimationend = () => div.remove();
		div.classList.add('fade-out');
	}, ms);
}
