import { Box, Typography } from '@mui/material';
import './board.css';

export const REFS = {
		boardWrapper: null,
		board: null,
		tiles: new Array(30).fill(null),
	},
	setBoardWrapperRef = setRef('boardWrapper'),
	setBoardRef = setRef('board'),
	setTileRef = (node, i) => {
		REFS.tiles[i] = node;
	};

function setRef(_for) {
	return node => {
		REFS[_for] = node;
		computeBoardDimensions();
	};
}

export function computeBoardDimensions() {
	const { boardWrapper, board, tiles } = REFS;

	if (board && boardWrapper && tiles) {
		const bww = boardWrapper.clientWidth,
			bwh = boardWrapper.clientHeight;

		let h = bwh,
			w = Math.floor((5 / 6) * h);

		if (w > bww) {
			w = bww;
			h = Math.floor((6 / 5) * w);
		}

		if (w > 375 /*|| h > 450 */) {
			w = 375;
			h = 450;
		}

		w = `${w}px`;
		h = `min(${h}px, 100%)`;
		// h = `${h}px`;

		board.setAttribute('style', `width: ${w}; height: ${h};`);
		board.style.width = w;
		board.style.height = h;
	}
}

const tileStyles = {
		transition: theme =>
			theme.transitions.create(['border-color'], { duration: 250 }),
	},
	boardStyles = {
		transition: theme =>
			theme.transitions.create(['width', 'height'], { delay: 200 }),
	},
	letterStyles = {
		fontWeight: 'bold',
		fontSize: '5vh',
		textTransform: 'uppercase',
		lineHeight: 1,
		userSelect: 'none',
	};

export default function Board(props) {
	return (
		<Box className='board-wrapper' sx={{ mx: 4 }} ref={setBoardWrapperRef}>
			<Box className='board' ref={setBoardRef} sx={boardStyles}>
				{props.state.map((letter, i) => (
					<Box
						className='tile'
						sx={tileStyles}
						key={i}
						ref={node => setTileRef(node, i)}
					>
						<Typography component='span' sx={letterStyles}>
							{letter}
						</Typography>
					</Box>
				))}
			</Box>
		</Box>
	);
}
