import { useState, useEffect, useCallback } from 'react';
import { Stack } from '@mui/material';

import { createTheme, ThemeProvider } from '@mui/material';
import localforage from 'localforage';

import enqueueSnackbar from './components/Snackbar';
import Header from './components/Header';
import KeyBoard from './components/Keyboard';

import Board, { REFS } from './components/Board';
import { targetWords } from './data/targetWords';
import { dictionary } from './data/dictionary';

import './app.css';
import './components/snackbar.css';

export function animate(e, removeClasses, ...classNames) {
	if (removeClasses)
		e.onanimationend = () => e.classList.remove(...classNames);
	e.classList.add(...classNames);
}

const err = () => enqueueSnackbar('Something went wrong'),
	getRandom = list => list[Math.floor(Math.random() * list.length)];

function updateStats(statistics, win, guessNo) {
	const { stats, guessDist } = statistics;

	stats.Played++;
	if (win) {
		guessDist[guessNo]++;
		stats['Max Streak'] = Math.max(
			++stats['Current Streak'],
			stats['Max Streak']
		);
	} else {
		stats['Current Streak'] = 0;
	}

	let nGamesWon = guessDist.reduce((c, e) => c + e, 0);
	stats['Win %'] = Math.round((nGamesWon / stats.Played) * 100);

	localforage.setItem('game-data', statistics).catch(err);
}

const $ = {
	stats: {
		Played: 0,
		'Win %': 0,
		'Current Streak': 0,
		'Max Streak': 0,
	},
	guessDist: new Array(6).fill(0),
	board: new Array(30).fill(''),
	position: [0, 0],
	colorHints: new Array(26).fill(''),
};

// export const SettingsContext = createContext();
const customTheme = createTheme({
	palette: {
		common: {
			green: { 100: '#6aaa64', 200: '#4b8246' },
			yellow: { 100: '#c9b458', 200: '#ab973f' },
			grey: { 100: '#787c7e', 200: '#5a5d5e' },
			orange: { 100: '#f5793a', 200: '#d96429' },
			blue: { 100: '#85c0f9', 200: '#679fd6' },
		},
	},
});

export default function App() {
	const [board, setBoard] = useState($.board),
		[position, setPosition] = useState($.position),
		[colorHints, setColorHints] = useState($.colorHints),
		[stats, setStats] = useState($.stats),
		[guessDist, setGuessDist] = useState($.guessDist),
		[openStats, setOpenStats] = useState(false),
		[gameOver, setGameOver] = useState(false),
		[guessNo, setGuessNo] = useState(-1),
		[targetWord, setTargetWord] = useState(getRandom(targetWords));
	// [userSettings, setUserSettings] = useState({
	// 	highContrast: false,
	// 	darkMode: false,
	// });

	const handleClose = useCallback(() => {
			setOpenStats(false);
			setGuessNo(-1);
		}, []),
		onOpenStats = useCallback(() => setOpenStats(true), []),
		onResetStats = useCallback(() => {
			setStats($.stats);
			setGuessDist($.guessDist);
			localforage.setItem('game-data', $).catch(err);
		}, []),
		startOver = useCallback(() => {
			setBoard($.board);
			setPosition($.position);
			setColorHints($.colorHints);
			setTargetWord(getRandom(targetWords));
			setGameOver(false);

			REFS.tiles.forEach(tile => (tile.className = 'tile'));
		}, []);

	function handleClick(e) {
		if (gameOver) return;

		const key = e.currentTarget.getAttribute('data-letter'),
			board_copy = board.slice();

		let [r, c] = position;

		if (key === 'enter') {
			let s = r * 5,
				e = (r + 1) * 5,
				word = board.slice(s, e).join(''),
				tiles = REFS.tiles.slice(s, e),
				colors = new Array(5).fill('green');

			if (word.length < 5) {
				// show an alert saying "not enough letters"
				enqueueSnackbar('Not enough letters');
				tiles.forEach(tile => animate(tile, true, 'head-shake'));
				return;
			}

			if (!dictionary.includes(word)) {
				// show an alert saying "word not in list"
				enqueueSnackbar('Not in word list');
				tiles.forEach(tile => animate(tile, true, 'head-shake'));
				return;
			}

			if (word !== targetWord) {
				let colorHints_ = colorHints.slice();

				for (let i = 0; i < 5; i++) {
					let color = 'grey';
					if (word[i] === targetWord[i]) color = 'green';
					else if (targetWord.includes(word[i])) color = 'yellow';

					colorHints_[word[i].charCodeAt() - 97] = color;
					colors[i] = color;
				}

				tiles.forEach((tile, i) =>
					animate(
						tile,
						false,
						`flip-tile-${colors[i]}`,
						`delay-${i * 100}`
					)
				);

				if (r < 5) {
					r++;
					c = 0;
					setPosition([r, c]);
					setColorHints(colorHints_);
				} else {
					// game over
					enqueueSnackbar(`<b>${targetWord}</b>`, 2500);

					const temp = { stats, guessDist: guessDist.slice() };
					updateStats(temp, false, null);
					setStats(temp.stats);
					setGuessDist(temp.guessDist);
					setGameOver(true);
					// open stats after 2000ms

					setTimeout(onOpenStats, 2000);
				}
				return;
			}

			// win YaY!!!
			tiles.forEach((tile, i) =>
				animate(tile, false, `flip-tile-${colors[i]}`, `delay-${i * 100}`)
			);
			enqueueSnackbar('Impressive !!', 2500);

			const temp = { stats, guessDist: guessDist.slice() };
			updateStats(temp, true, r);
			setStats(temp.stats);
			setGuessDist(temp.guessDist);
			setGuessNo(r);
			setGameOver(true);

			// open stats after 2000ms
			setTimeout(onOpenStats, 2000);
		} else if (key === 'backspace') {
			// remove the character that was added last
			if (c > 0) {
				board_copy[r * 5 + --c] = '';
				setBoard(board_copy);
				setPosition([r, c]);

				const tile = REFS.tiles[r * 5 + c];
				tile.classList.remove('filled');
			}
		} else {
			// add the character to current row
			if (c < 5) {
				board_copy[r * 5 + c++] = key;
				setBoard(board_copy);
				setPosition([r, c]);

				const tile = REFS.tiles[r * 5 + (c - 1)];
				animate(tile, true, 'rubber-band');
				tile.classList.add('filled');
			}
		}
	}

	useEffect(() => {
		localforage.config({
			name: 'wordle-clone',
		});

		localforage.getItem('game-data', (err, val) => {
			if (err) return;
			if (val) {
				setStats(val.stats);
				setGuessDist(val.guessDist);
			}
		});
	}, []);

	return (
		// <SettingsContext.Provider value={[userSettings, setUserSettings]}>
		<ThemeProvider theme={customTheme}>
			<Stack justifyContent='space-between' className='app'>
				<Header
					onClick={onOpenStats}
					open={openStats}
					onClose={handleClose}
					stats={stats}
					guessDist={guessDist}
					highlight={guessNo}
					onReset={onResetStats}
					onNewGame={startOver}
				/>
				<Board state={board} />
				<KeyBoard onClick={handleClick} colorHints={colorHints} />
			</Stack>
		</ThemeProvider>
		// </SettingsContext.Provider>
	);
}
