import {
	Dialog as MUIDialog,
	DialogContent,
	DialogTitle,
	IconButton,
	Box,
	Typography,
	Divider,
} from '@mui/material';
import { Close, Favorite } from '@mui/icons-material';

const tileStyles = {
	width: 45,
	height: 45,
	mr: 0.5,
	fontSize: 'h5.fontSize',
	fontWeight: 'bold',
};

const Tiles = ({ classes, word, index, children }) => (
	<>
		<Box sx={{ display: 'flex', mt: 3, mb: 1 }}>
			{word.split('').map((letter, i) => (
				<Box
					sx={tileStyles}
					className={`tile ${i === index ? classes : ''}`}
					key={i}
				>
					<span>{letter}</span>
				</Box>
			))}
		</Box>
		{children}
	</>
);

export function Dialog({ title, children, ...props }) {
	return (
		<MUIDialog maxWidth='sm' fullWidth {...props}>
			<DialogTitle
				sx={{
					textAlign: 'center',
					fontWeight: 'bold',
					textTransform: 'uppercase',
				}}
			>
				{title}
				<IconButton
					sx={{ position: 'absolute', right: 8, top: 12 }}
					onClick={props.onClose}
				>
					<Close />
				</IconButton>
			</DialogTitle>
			{children}
		</MUIDialog>
	);
}

export default function InstructionsDialog(props) {
	return (
		<Dialog {...props} title='How to play'>
			<DialogContent>
				<Typography component='div'>
					<Box sx={{ mb: 2 }}>
						Guess the <b>WORDLE</b> in 6 tries.
					</Box>
					<Box sx={{ mb: 2 }}>
						Each guess must be a valid 5-letter word. Hit the enter button
						to submit.
					</Box>
					<Box sx={{ mb: 2 }}>
						After each guess, the color of the tiles will change to show
						how close your guess was to the word.
					</Box>
					<Divider />

					<Box sx={{ mt: 2 }}>
						<b>EXAMPLES</b>
					</Box>

					<Tiles
						word='WEARY'
						index={0}
						classes='flip-tile-green delay-200'
					>
						The letter <b>W</b> is in the word and in the correct spot.
					</Tiles>

					<Tiles
						word='PILLS'
						index={1}
						classes='flip-tile-yellow delay-300'
					>
						The letter <b>I</b> is in the word but in the wrong spot.
					</Tiles>

					<Tiles word='VAGUE' index={3} classes='flip-tile-grey delay-400'>
						The letter <b>U</b> does not appear in the word.
					</Tiles>
					<Divider sx={{ mt: 3 }} />
					<Banner />
				</Typography>
			</DialogContent>
		</Dialog>
	);
}

export const Banner = () => (
	<Box
		sx={{
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			color: 'grey.700',
			mt: 2,
		}}
	>
		<span>crafted with</span> <Favorite fontSize='small' sx={{ mx: 0.5 }} />{' '}
		<span>
			by <b>atharvaroskar</b>
		</span>
	</Box>
);
